// Put any @imports here
// See Readme for a list of supported ones

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

body {
  background: #e2ebf3;

  .likes {
    margin: 10px;
    width: 125px;
    height: 50px;
    background-color: #2194E0;
    color: white;
    border: 0;
    border-radius: 8px;
    box-shadow: 0 5px #187DBC;
    outline: 0;
  }

  .likes:active {
    box-shadow: 0 0;
    margin-top: 15px;
  }
}

